import axios from "axios";

let table = $('#urgent-needs-table'),
    changeStatusButton = $('.change-status');

function initTable() {
    table.DataTable({
        autoWidth: false,
        pageLength: 25,
        colReorder: true,
        language: {
            lengthMenu: '_MENU_',
            search: "_INPUT_",
            searchPlaceholder: 'Search for anything in the table...'
        },
        "sDom": 'Rfrtlip'
    });
}

function initToolTip() {
    $('[data-toggle="tooltip"]').tooltip({container: 'body'});
}

function updateStatus() {
    changeStatusButton.click(function () {
        let button = $(this);
        let data;
        if (button.hasClass('mark-pending')) {
            data = {pending: true}
        } else if (button.hasClass('mark-complete')) {
            data = {complete: true}
        }
        axios.post(button.data('route'), data)
            .then(response => {
                toastr.success('The need has been updated.');
                loader.reload();
            })
            .catch(error => {
                console.log(error);
            });
    });
}

function initPopover() {
    $('[data-toggle="popover"]').popover();
}

function init() {
    initTable();
    initToolTip();
    initPopover();
    updateStatus();
}

init();


